.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);

  display: flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  pointer-events: none;

  transition-property: opacity, backdrop-filter;
  transition-duration: .35s;
  transition-timing-function: cubic-bezier(.2,.7,.2,1);

  // Состояние открытия (поддержка обоих модификаторов)
  &.active,
  &.modal--active {
    opacity: 1;
    pointer-events: all;
  }

  &__content,
  &__img {
    opacity: 0;
    transform: translateY(2.4rem) scale(.98);
    transition-property: opacity, transform;
    transition-duration: .55s, .55s;
    transition-timing-function: cubic-bezier(.2,.7,.2,1);
    will-change: transform, opacity;
  }

  // Появление с лёгким пружинящим эффектом
  &.active &__content,
  &.modal--active &__content {
    opacity: 1;
    transform: translateY(0) scale(1);
    transition-delay: .08s;
  }

  // Лёгкая задержка для изображения — создаёт эффект ступенчатого появления
  &.active &__img,
  &.modal--active &__img {
    opacity: 1;
    transform: translateY(0) scale(1);
    transition-delay: .08s;
  }

  // ===== Альтернативный вариант анимации (3D flip) =====
  &.modal--alt {
    perspective: 120rem;
    perspective-origin: 50% 30%;

    &__content,
    &__img {
      opacity: 0;
      transform: translateY(1.2rem) scale(.96) rotateX(-8deg);
      transform-origin: 50% 0%;
      transition-property: opacity, transform;
      transition-duration: .55s, .7s;
      transition-timing-function: cubic-bezier(.16,.84,.44,1);
      backface-visibility: hidden;
    }

    &.active &__content,
    &.modal--active &__content {
      opacity: 1;
      transform: translateY(0) scale(1) rotateX(0);
      animation: modal-flip-in .6s cubic-bezier(.16,.84,.44,1);
    }

    &.active &__img,
    &.modal--active &__img {
      opacity: 1;
      transform: translateY(0) scale(1) rotateX(0);
      transition-delay: .06s;
    }

    &__close {
      @include hover {
        // Лёгкий «вибро»-эффект при наведении
        animation: modal-btn-wobble .5s ease both;
      }
    }
  }

  &__body {
    max-width: 93.75%;
    width: 100%;
    position: relative;
    display: flex;
    gap: 2rem;
    min-height: 80rem;

    @include mobile {
      min-height: unset;
    }
  }
  &__close {
    position: absolute;
    top: 2.6rem;
    right: 3.2rem;
    z-index: 10;

    width: 2.8rem;
    height: 2.8rem;
    color: var(--pl-white);

    transform: scale(1) rotate(0deg);
    transition-property: color, transform;
    transition-duration: .3s, .35s;
    transition-timing-function: ease, cubic-bezier(.2,.7,.2,1);

    @include mobile {
      color: var(--pl-black);
      top: 4rem;
      right: 3rem;
      width: 2rem;
      height: 2rem;
    }

    svg {
      width: 100%;
      height: 100%;
      stroke-width: 3px;
      stroke: currentColor;
    }

    @include hover {
      color: var(--pl-blue);
      transform: scale(1.05) rotate(90deg);
    }

    &:active {
      transform: scale(.96) rotate(90deg);
      transition-duration: .12s;
    }
  }

  // Контент карточки
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
    border-radius: .5rem;
    padding: 8rem;
    background-color: var(--pl-white);
    // тень проявляется вместе с анимацией появления
    box-shadow: 0 1.6rem 4.8rem rgba(0,0,0,.12);

    @include mobile {
      padding: 3rem;
    }
  }

  &__title {
    margin-bottom: 5rem;

    @include mobile {
      font-size: 3.6rem;
      padding-right: 4rem;
      margin-bottom: 4rem;
    }
  }

  &__img {
    border-radius: .5rem;
    background-color: var(--pl-white);
    overflow: hidden;
    max-width: 70.7rem;
    width: 100%;
    position: relative;
    flex-shrink: 0;

    @include mobile {
      display: none;
    }
    img {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  // ===== Модальное окно успешной отправки =====
  &-success__content {
    text-align: center;
    align-items: center;
    justify-content: center;
    max-width: 50rem;
    width: 100%;
    margin: 0 auto;
    padding: 6rem 4rem;

    @include mobile {
      padding: 4rem 3rem;
      max-width: 100%
    }
  }

  &-success__icon {
    margin-bottom: 3rem;
    animation: modal-success-bounce 0.6s ease-out;

    @include mobile {
      margin-bottom: 2rem;

      svg {
        width: 60px;
        height: 60px;
      }
    }
  }

  &-success__title {
    margin-bottom: 2rem;
    color: #333;
    padding-right: 0;
    @include mobile {
      font-size: 3.4rem;
      margin-bottom: 1.5rem;
    }
  }

  &-success__text {
    font-size: 1.8rem;
    line-height: 1.6;
    color: #666;
    margin-bottom: 3rem;
    max-width: 400px;

    @include mobile {
      font-size: 2.2rem;
      margin-bottom: 2.5rem;
    }
  }

  &-success__btn {
    min-width: 200px;
    padding: 1.5rem 3rem;
    font-size: 1.6rem;
    border-radius: 8px;
    transition: all 0.3s ease;

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
    }

    &:active {
      transform: translateY(0);
    }

    @include mobile {
      min-width: 100%;
      padding: 1.2rem 2.5rem;
      font-size: 2.2rem;
    }
  }
}

// Анимация для иконки успеха
@keyframes modal-success-bounce {
  0% {
    transform: scale(0) rotate(-180deg);
    opacity: 0;
  }
  50% {
    transform: scale(1.2) rotate(-90deg);
    opacity: 1;
  }
  70% {
    transform: scale(0.9) rotate(0deg);
  }
  100% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
}

